@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,800;1,400&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html , body{
    position: relative;
    width: 100%;
    height: 100%;
}
body{
    background: #36D1DC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #5B86E5, #36D1DC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-family: 'Poppins', sans-serif;
}
#root{
    position: relative;
    width: 100%;
    height: 100%;
}
.HangmanImg{
    margin: 20px 0;
    border: 3px solid #0003;
    height: 240px;
}
.hangmangame{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.hangmangame .msg{
    font-weight: bolder;
    height: 30px;
    letter-spacing: 2px;
    font-size: 22px;
}
.hangmangame .text{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
    font-weight: bolder;
    width: 280px;
    margin-bottom: 30px;
    margin-top: 20px;
}
.hangmangame .text p{
    position: relative;
    width: 36px;
    height: 36px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
    color: #fff;
    border: 2px solid #fff;
    font-family: 'Courier New', Courier, monospace;
}
.hangmangame .text p.red{
    color: red;
}
.hangmangame .keyboard{
    position: relative;
    width: 450px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.hangmangame .keyboard button{
    border: none;
    background: none;
    outline: none;
    position: relative;
    width: 34px;
    height: 34px;
    text-align: center;
    line-height: 34px;
    margin: 4px;
    background: #000;
    color: #fff;
    cursor: pointer;
    transition: 0.4s;
    font-weight: bolder;
}
.hangmangame .keyboard button:hover,
.hangmangame .keyboard button:disabled{
    color: #000;
    background: #fff;
    box-shadow: 0 0 5px 2px #0006;
}
button.restart{
    padding: 10px 26px;
    margin-top: 20px;
    background: none;
    border: none;
    outline: none;
    border: 1.75px solid #000;
    cursor: pointer;
    font-weight: bolder;
    transition: 0.5s;
    letter-spacing: 2px;
    margin-bottom: 30px;
}
button.restart:hover{
    background: #000;
    color: #fff;
}
@media screen and (max-width:480px){
    .hangmangame .keyboard{
        width: 90%;
    }
}
@media screen and (max-width:520px){
    .hangmangame{
        position: relative;
        max-width: 100%;
    }
}
.App{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 40px;
}
.App h1{
    letter-spacing: 4px;
}
@media screen and (max-width : 520px){
    .App h1{
        font-size: 22px;
    }
}
